<template>
	<transition
		:css="false"
		appear
		@before-enter="_before_enter"
		@enter="_enter"
		@leave="_leave"
	>
		<div class="flex-center background">
			<div style="width: 144vh;height:74%;margin-bottom: 3.4%;" @click="_go">
				<img src="../../assets/img/XLC-OP-1002 18kV一次主接线图.png" style="width: 100%;height: 100%;"/>
			</div>
		</div>
	</transition>
</template>

<script>
	import animate from 'velocity-animate/velocity';
	export default {
		data() {
			return {
				
			}
		},
		methods: {
			_before_enter(el){
				el.style.opacity = 0;
			},
			_enter(el,done){
				animate(el,{ opacity: 1 },{ duration: 1200, complete: done });
			},
			_leave(el,done){
				animate(el,{ opacity: 0},{ duration: 600, complete: done});
			},
			_go(){
				this.$router.push('/listdetail');
			}
		}
	}
</script>

<style scoped>
	.background {
		width: 100%;
		height: 100%;
		background-image: url('../../assets/img/back2.png');
		background-position: center center;
		background-size: auto 100%;
		color: #fff;
	}
</style>
